<h1 id="自定义菜单" tabindex="-1"><a class="header-anchor" href="#自定义菜单" aria-hidden="true">#</a> 自定义菜单</h1>
<h2 id="读取-查询-已设置菜单" tabindex="-1"><a class="header-anchor" href="#读取-查询-已设置菜单" aria-hidden="true">#</a> 读取（查询）已设置菜单</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$list</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="获取当前菜单" tabindex="-1"><a class="header-anchor" href="#获取当前菜单" aria-hidden="true">#</a> 获取当前菜单</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$current</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">current</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="添加菜单" tabindex="-1"><a class="header-anchor" href="#添加菜单" aria-hidden="true">#</a> 添加菜单</h2>
<h3 id="添加普通菜单" tabindex="-1"><a class="header-anchor" href="#添加普通菜单" aria-hidden="true">#</a> 添加普通菜单</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$buttons</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span>
        <span class="token string double-quoted-string">"type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"click"</span><span class="token punctuation">,</span>
        <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"今日歌曲"</span><span class="token punctuation">,</span>
        <span class="token string double-quoted-string">"key"</span>  <span class="token operator">=></span> <span class="token string double-quoted-string">"V1001_TODAY_MUSIC"</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span>
        <span class="token string double-quoted-string">"name"</span>       <span class="token operator">=></span> <span class="token string double-quoted-string">"菜单"</span><span class="token punctuation">,</span>
        <span class="token string double-quoted-string">"sub_button"</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token punctuation">[</span>
                <span class="token string double-quoted-string">"type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"view"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"搜索"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"url"</span>  <span class="token operator">=></span> <span class="token string double-quoted-string">"http://www.soso.com/"</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
            <span class="token punctuation">[</span>
                <span class="token string double-quoted-string">"type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"view"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"视频"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"url"</span>  <span class="token operator">=></span> <span class="token string double-quoted-string">"http://v.qq.com/"</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
            <span class="token punctuation">[</span>
                <span class="token string double-quoted-string">"type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"click"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"name"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"赞一下我们"</span><span class="token punctuation">,</span>
                <span class="token string double-quoted-string">"key"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"V1001_GOOD"</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$buttons</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br></div></div><p>以上将会创建一个普通菜单。</p>
<h3 id="添加个性化菜单" tabindex="-1"><a class="header-anchor" href="#添加个性化菜单" aria-hidden="true">#</a> 添加个性化菜单</h3>
<p>与创建普通菜单不同的是，需要在 <code>create()</code> 方法中将个性化匹配规则作为第二个参数传进去：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$buttons</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token comment">// ...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$matchRule</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string double-quoted-string">"tag_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"2"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"sex"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"1"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"country"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"中国"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"province"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"广东"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"city"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"广州"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"client_platform_type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"2"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"language"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"zh_CN"</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$buttons</span><span class="token punctuation">,</span> <span class="token variable">$matchRule</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h2 id="删除菜单" tabindex="-1"><a class="header-anchor" href="#删除菜单" aria-hidden="true">#</a> 删除菜单</h2>
<p>有两种删除方式，一种是<strong>全部删除</strong>，另外一种是<strong>根据菜单 ID 来删除</strong>(删除个性化菜单时用，ID 从查询接口获取)：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 全部</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$menuId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="测试个性化菜单" tabindex="-1"><a class="header-anchor" href="#测试个性化菜单" aria-hidden="true">#</a> 测试个性化菜单</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">menu</span><span class="token operator">-></span><span class="token function">match</span><span class="token punctuation">(</span><span class="token variable">$userId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p><code>$userId</code> 可以是粉丝的 OpenID，也可以是粉丝的微信号。</p>
</blockquote>
<p>返回 <code>$menu</code> 与指定的 <code>$userId</code> 匹配的菜单项。</p>
